@font-face {
  font-family: "GmarketSansLight";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansLight.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

.container {
  padding: 0 100px;
  width: 100%;
  height: 200px;
  background: rgb(225, 194, 221);
  background: -moz-linear-gradient(
    0deg,
    rgba(225, 194, 221, 1) 0%,
    rgba(201, 231, 249, 1) 47%
  );
  background: -webkit-linear-gradient(
    0deg,
    rgba(225, 194, 221, 1) 0%,
    rgba(201, 231, 249, 1) 47%
  );
  background: linear-gradient(
    0deg,
    rgba(225, 194, 221, 1) 0%,
    rgba(201, 231, 249, 1) 47%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#e1c2dd",endColorstr="#c9e7f9",GradientType=1);
}

.down__memo {
  padding-top: 100px;
  font-size: 10px;
  font-family: "GmarketSansLight";
  text-align: end;
  text-indent: 1px;
}

.btn__box {
  text-align: right;
}
.upBtn {
  padding: 0 25px;
  font-size: 30px;
  font-weight: 700;
  background-color: transparent;
  border: none;
  border-radius: 50%;
}

.upBtn:hover {
  background: linear-gradient(to right, rgb(40, 85, 209), rgb(73, 198, 171));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
