@font-face {
  font-family: "GmarketSansLight";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansLight.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "GmarketSansMedium";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "NEXON Lv1 Gothic OTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
}

.container {
  padding: 0 100px;
  width: 100%;
  height: 600px;
  text-align: center;
  background: rgb(255, 255, 255);
  background: -moz-linear-gradient(
    0deg,
    rgba(255, 255, 255, 1) 17%,
    rgba(201, 231, 249, 1) 30%,
    rgba(221, 215, 240, 1) 54%,
    rgba(225, 194, 221, 1) 94%
  );
  background: -webkit-linear-gradient(
    0deg,
    rgba(255, 255, 255, 1) 17%,
    rgba(201, 231, 249, 1) 30%,
    rgba(221, 215, 240, 1) 54%,
    rgba(225, 194, 221, 1) 94%
  );
  background: linear-gradient(
    0deg,
    rgba(255, 255, 255, 1) 17%,
    rgba(201, 231, 249, 1) 30%,
    rgba(221, 215, 240, 1) 54%,
    rgba(225, 194, 221, 1) 94%
  );
}

.title {
  padding-top: 100px;
  font-size: 10vh;
  font-family: "NEXON Lv1 Gothic OTF";
  animation: title 1.5s ease-in-out forwards 0s;
  background: linear-gradient(to right, rgb(240, 163, 205), rgb(107, 151, 197));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.intro {
  font-family: "NEXON Lv1 Gothic OTF";
  font-size: 4vh;
  padding: 10px;
  animation: title 1.5s ease-in-out forwards 0s;
  background: linear-gradient(to right, rgb(240, 163, 205), rgb(107, 151, 197));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.intro p strong {
  font-size: 30px;
}
.bubble {
  width: 110px;
  height: 110px;
  background-color: transparent;
}

.btn_box {
  width: 100%;
  height: 20vh;
  display: flex;
  justify-content: center;
}

.btn {
  margin-top: 70px;
  padding: 30px;
  font-size: 30px;
  font-family: "GmarketSansLight";
  border: none;
  background-color: transparent;
}

.btn:hover {
  font-family: "GmarketSansMedium";
}

@keyframes title {
  0% {
    transform: translateX(-30px);
    opacity: 0;
  }
  100% {
    transform: translateX(0px);
    opacity: 1;
  }
}
