@font-face {
  font-family: "GmarketSansLight";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansLight.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "GmarketSansMedium";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

.container {
  margin: 0;
  padding: 0 100px;
  width: 100%;
}

.title {
  margin: 100px 0;
  font-size: 70px;
  font-weight: 600;
  font-family: "GmarketSansLight";
  text-align: right;
}

.description {
  font-family: "GmarketSansLight";
  text-align: left;
  text-indent: 2%;
}

.first__des {
  padding: 20px 0;
  text-indent: 0%;
}

.cv__box {
  padding-top: 20px;
  text-align: center;
}

.cvBtn {
  margin: 20px;
  padding: 10px 30px;
  background-color: transparent;
  border: 0.4px solid darkgray;
  border-radius: 15px;
}

.cvBtn a {
  font-family: "GmarketSansLight";
  text-decoration: none;
  color: black;
}

.cvBtn a:hover {
  font-family: "GmarketSansMedium";
}
