@font-face {
  font-family: "GmarketSansLight";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansLight.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "GmarketSansMedium";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

.container {
  margin: 0;
  padding: 0 100px;
  width: 100%;
}

.title {
  margin: 100px 0;
  font-size: 70px;
  font-weight: 600;
  font-family: "GmarketSansLight";
  text-align: right;
}

.workbox {
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.content {
  max-width: 500px;
  font-family: "GmarketSansLight";
  text-align: center;
}

.content h2 {
  padding-bottom: 20px;
}

.btn {
  margin: 20px;
  padding: 10px 15px;
  font-family: "GmarketSansLight";
  font-size: 20px;
  background-color: transparent;
  border: 0.4px solid darkgray;
  border-radius: 15px;
}

.btn:hover {
  font-family: "GmarketSansMedium";
}

.btn a {
  text-decoration: none;
  color: black;
}
