@font-face {
  font-family: "GmarketSansLight";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansLight.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "GmarketSansMedium";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

.container {
  margin: 0;
  padding: 0 100px;
  width: 100%;
  background: rgb(201, 231, 249);
  background: -moz-linear-gradient(
    0deg,
    rgba(201, 231, 249, 1) 0%,
    rgba(255, 255, 255, 1) 100%
  );
  background: -webkit-linear-gradient(
    0deg,
    rgba(201, 231, 249, 1) 0%,
    rgba(255, 255, 255, 1) 100%
  );
  background: linear-gradient(
    0deg,
    rgba(201, 231, 249, 1) 0%,
    rgba(255, 255, 255, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#c9e7f9",endColorstr="#ffffff",GradientType=1);
}

.title {
  margin: 100px 0;
  padding-left: 10vw;
  font-size: 70px;
  font-weight: 600;
  font-family: "GmarketSansLight";
  text-align: left;
}

.img__box {
  padding: 0;
  text-align: center;
}

.img__box img {
  padding: 10px;
  width: 50px;
  height: 50px;
}

.img__box img:hover {
  transform: scale(0.9);
}

.gmail,
.phone {
  padding: 10px;
  font-family: "GmarketSansMedium";
  font-weight: 700;
}
