@font-face {
  font-family: "GmarketSansLight";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansLight.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

.container {
  margin: 0;
  padding-left: 10vw;
  width: 100%;
}

.title {
  margin: 100px 0;
  font-size: 70px;
  font-weight: 600;
  font-family: "GmarketSansLight";
  text-align: left;
}

.skill__block {
  margin: 50px 0;
  padding: 20px 0;
  min-width: 50vh;
  text-align: left;
}

.description {
  text-indent: 5px;
  font-family: "GmarketSansLight";
}
.description p {
  padding-bottom: 3px;
}

.img__box {
  width: 100%;

  margin-top: 50px;
  text-align: right;
}

.skill__block .image {
  margin: 15px 7px 5px 7px;
  padding-top: 5px;
  width: 100px;
  height: 100px;
}

.skill__block .image2 {
  margin: 15px 7px 5px 7px;
  padding-top: 5px;
  width: 150px;
  height: 100px;
}

.skill__block .image:hover {
  transform: scale(1.3);
}

.skill__block .image2:hover {
  transform: scale(1.3);
}
